<template>
    <div class="input-group color-picker" ref="colorpicker">
        <label class="col-form-label col-form-label-sm" :for="label">{{label}}</label>
        <input type="text" class="form-control form-control-sm" v-model="colorValue" @focus="showPicker()"
               @input="updateFromInput"/>
        <chrome-picker :value="colors" @input="updateFromPicker" v-if="displayPicker"/>
    </div>
</template>
<script>

    import {Chrome} from 'vue-color'

    export default {
        name: 'colorpicker',
        components: {
            'chrome-picker': Chrome,
        },
        props: ['color', 'label'],
        data() {
            return {
                colors: {
                    hex: '#000000',
                },
                colorValue: '',
                displayPicker: false,
            }
        },
        mounted() {
            this.setColor(this.color || '#000000');
        },
        methods: {
            setColor(color) {
                this.updateColors(color);
                this.colorValue = color;
            },
            updateColors(color) {
                if (color.slice(0, 1) == '#') {
                    this.colors = {
                        hex: color
                    };
                } else if (color.slice(0, 4) == 'rgba') {
                    var rgba = color.replace(/^rgba?\(|\s+|\)$/g, '').split(','),
                        hex = '#' + ((1 << 24) + (parseInt(rgba[0]) << 16) + (parseInt(rgba[1]) << 8) + parseInt(rgba[2])).toString(16).slice(1);
                    this.colors = {
                        hex: hex,
                        a: rgba[3],
                    }
                }
            },
            showPicker() {
                document.addEventListener('click', this.documentClick);
                this.displayPicker = true;
                this.$eventHub.$emit('partymeister-slides:open-color-picker');
            },
            hidePicker() {
                document.removeEventListener('click', this.documentClick);
                this.displayPicker = false;
            },
            togglePicker() {
                this.displayPicker ? this.hidePicker() : this.showPicker();
            },
            updateFromInput() {
                this.updateColors(this.colorValue);
            },
            updateFromPicker(color) {
                this.colors = color;
                if (color.rgba.a == 1) {
                    this.colorValue = color.hex;
                } else {
                    this.colorValue = 'rgba(' + color.rgba.r + ', ' + color.rgba.g + ', ' + color.rgba.b + ', ' + color.rgba.a + ')';
                }
            },
            documentClick(e) {
                var el = this.$refs.colorpicker,
                    target = e.target;
                if (el !== target && !el.contains(target)) {
                    this.hidePicker()
                }
            }
        },
        watch: {
            color(val) {
                this.setColor(this.color);
            },
            colorValue(val) {
                if (val) {
                    this.updateColors(val);
                    this.$eventHub.$emit('color-picker:value-change', val);
                    this.$emit('input', val);
                }
            }
        }
    };
</script>
<style>
    .vc-chrome {
        position: absolute;
        z-index: 10000;
        top: 30px;
        right: 0;
    }
</style>
